import { Component } from '@angular/core';
import { OnInit, NgZone, Input, OnChanges, SimpleChanges, ViewEncapsulation} from '@angular/core';

import { MessengerService } from '../../../../messenger.service';

import './ckeditor.loader';
import 'ckeditor';

@Component({
  selector: 'ckeditor-component',
  templateUrl: './ckeditor.html',
  styleUrls: ['./ckeditor.scss'],
  encapsulation: ViewEncapsulation.None,

})

export class Ckeditor {
  public ckeditorContent:string = 'to main\n\nend';
  public config = {
    uiColor: '#F0F3F4',
    height: '600',
  };

  constructor(private _messengerService: MessengerService) {
  }

  ngOnInit() {
    this._messengerService.initChrome();
  }

  public sendCmd(message){
    this._messengerService.sendCmd(message);
  }

  handleButtonDownload () {
    console.log(this.ckeditorContent);
    this._messengerService.sendLogo(this.ckeditorContent);
  }
}



